.data {
    border-top: 8px solid rgb(248,232,208);
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    letter-spacing: -1px;
    height: 364px;
    width: 472px;
}
.yellow-border-top {
    height: 100%;
    border-top: 8px solid rgba(248, 240, 192, 1) !important;
}

.data--item{
    display: flex;
}


.item--left{
    -webkit-text-stroke: 2px rgb(120, 128, 144);
    color: rgb(255, 255, 255);
    font-weight: bolder;
    position: relative;
    display: flex;
    justify-content: center;
    margin-left: 12px;
    z-index: 10;
    width: 160px;
    align-items: flex-start;
}
.item--right{
    width: 304px !important;
}

/* .data--item--left {} */

.item--left::before {
    content: "";
    position: absolute;
    height: 16px;
    width: 160px;
    background: rgb(120, 128, 144);
    top: 12px;
    left: 0px;
    border-radius: 4px;
    z-index: -1;
}

.data--item--right {
    background-color: rgb(248, 240, 232);
    border-radius: 4px;
    width: 304px;
    padding-left: 16px;
    margin-left: -8px;
}

.type {
    height: inherit;
    display: flex;
    flex-direction: row;
}

.type b {
    border-radius: 4px;
    margin-right: 12px;
    padding: 4px 8px 0px 8px;
    width: 140px!important;
}
.type p b {
    border-radius: 4px;
    margin-right: 12px;
    padding: 4px 8px 0px 8px;
    width: 140px !important;
}

.skill--item{
    display: flex;
    justify-content: space-between; 
    margin-top: -8px;
    height: 40px;
}
.skill--item--left{
    
}
.skill--item--right{
    position: relative;top: -4px;
    background-color: rgb(248,248,216);
    border-radius: 8px;
    width: 152px;
    display: flex;
    justify-content: flex-end;
    padding-right: 8px;
    margin-right: 8px;
}

.hp{
    width: 300px!important;
}

.health-bar{
    display: flex;
    align-items:center;
    color: rgb(248, 176, 064);
    background-color:rgb(096,104,120);
    border-radius:8px;
    height: 20px;
    font-size: 20px;
    width:280px;
    margin-top:-16px;
    margin-left: 164px
}
.health-bar span{
    margin-left: 12px;
        display:inline-block;
        transform:scale(2, 1);
        /* W3C */
        -webkit-transform:scale(2, 1);
        /* Safari and Chrome */
        -moz-transform:scale(2, 1);
        /* Firefox */
        -ms-transform:scale(2, 1);
        /* IE 9 */
        -o-transform:scale(2, 1);
}

#progressbar {
    margin-left: 16px;
    outline: 4px solid white;
    width: 224px;
    background-color: rgb(096, 104, 120);
    border-radius: 12px;
    /* (height of inner div) / 2 + padding */

}

#progressbar>div {
    background-color: rgb(112, 248, 168);
    /* width: 0%; */
    /* Adjust with JavaScript */
    height: 8px;
    border-radius: 10px;
}