.move-list{
    position: relative;
    margin-left: -8px;
    width: 100%;
    background-color: rgba(152,232,208,1) !important;
    background-image: linear-gradient(0deg, rgba(152,232,208,1) 50%, rgba(168,240,216,1) 50%) !important;
    background-size: 10px 10px !important;
    border-left: 8px solid rgb(096, 104, 120);
    padding-top: -8px;
    padding-left: 8px;
    box-shadow: inset 8px 8px #e0f8f8
}

.move-item{
    position: relative;
    /* top: -4px; */
    height: 92px;
    margin: 20px 8px 0px 16px;
    background-color: rgb(240,248,248);
    border-radius: 8px;
    display: flex;
}
.move-type{
    width: 140px;
    position: relative
}

.move-type b{
    position: absolute;
    top: -4px;
    left:-16px;
    height: 40px;
    width:116px;
    font-size: 36px;
    display: flex;
    align-items: center;
    justify-content:center;
    border-radius: 8px;
}

.move-data{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 2;
}
.move-pp{
    align-self: flex-end;
}