.data-memo {
    height: 180px;
    width: 96%;
    background-color: rgb(248, 240, 232);
    align-self: center;
    border-radius: 8px;
    padding-top: 12px;
    padding: 12px 8px 0px 8px;
    position: relative
}

.data-memo p {
    padding-top: 16px;
}

.data-memo hr {
    margin-top: -4px;
    border: 2px solid rgb(232, 232, 191);
    border-radius: 4px;
}

.data-memo .data--item--left {
    position: absolute;
    width: 260px !important;
    top: -44px;
    left: 0px
}

.data-memo .data--item--left::before {
    width: 300px !important;
    top: 28px;
    left: -16px;
}
 
.underlined{
    position: relative;
    width: auto;
    top: 0px;
    left: 0px;
}
.underlined::before{
    content:"";
    position: absolute;
    top: 52px;
    left: 0px;
    height: 4px;
    width:100%;
    background-color:rgb(232, 232, 191);
    border-radius: 1px;
}


/* skills bottom styling */
.skills-bottom{
    width: 100%;
    height: 100%;
    border-left: 8px solid rgb(248, 240, 192)
}

.bottom__item{
    padding-top: 12px;
    width: 100%;
    display: flex;
    align-items: center;
}
.bottom__item--key{
    -webkit-text-stroke: 2px rgb(120, 128, 144);
    color: rgb(255, 255, 255);
    font-weight: bolder;
    height:100%;

    position: relative;    
    z-index: 10;
    
    display: flex;    
    flex-basis: 240px;
    align-items: flex-start;
    justify-content: center;
    flex-grow:0;
    flex-shrink: 0;

    margin-left: 6px;
    margin-right: -6px;
    margin-top:-16px;

}
.bottom__item--key::before{
    content: "";
    position: absolute;
    height: 16px;
    width: 240px;
    background: rgb(120, 128, 144);
    top: 12px;
    left: 0px;
    border-radius: 4px;
    z-index: -1;
}
.bottom__item--value-container{
    display:flex;
    flex-direction: column;
    flex-grow:1;
    height:auto;
    position: relative;
    height:100px;
    margin-right: 8px;
}
.bottom__item--value-container :first-child{
    border-top-left-radius: 8px;
}
.bottom__item--value-container :nth-child(4) {
    border-bottom-left-radius: 8px;
}

.bottom__item--value{
    /* border:2px solid black; */
    display: flex;
    justify-content: space-between;
    background-color: rgb(248, 248, 160);
    height: 48px;
}

.value__key{
    margin-left: 28px;
}
.value__value{
    background-color: rgb(248,248,216);
    width: 292px;
    display: flex;
    justify-content: flex-end;
    padding-right: 4px;
}

hr{
    position: absolute;
    top: 44px;
}
.hr1{
    width: 400px;
    border: 1px solid white;
    left: 24px;
}
.hr2{
    border: 1px solid rgb(240, 232, 176);
    left: 396px;
