.ability{
    width: 376px;
    position: relative;
    top:-8px;
    border-radius: 8px;
    padding-left: 28px;
    background-color: rgb(248, 248, 216) !important;
    z-index:5;
}
.ability-description{
    position: relative;
    top: -20px;
    z-index: 1; 
    margin: auto;
    width: 97.5%;
    height: 72px;
    border-radius: 8px;
    display: flex;
    align-items: flex-end;
    padding-bottom: 8px;
    padding-left: 12px;
}
.exp-bar{
    display: flex;
    align-items:center;
    position: absolute;
    top:96px;
    left: 384px;
    width:296px;
    height: 28px;
    background-color:rgb(096,104,120);
    color: rgb(248, 176, 064);
    border-radius: 12px;
    font-size: 24px;
    padding-left: 4px;
}

.bar__content{
    margin-left: 4px;
    width: 248px;
    border: 4px solid white;
    border-radius: 8px;
    border-top-left-radius: 0;
    height: 20px;
    background-color:rgb(152,144,112)
}

.bar__content>div{
    background-color: rgb(136, 200, 248);
    /* Adjust with JavaScript */
    height: 100%;
    border-bottom-left-radius: 8px;
    margin-left:-1px;
}